export function getNumericSuffixFor(value, includeDate) {
	var value = value.toString()
	var lastNumber = value.slice(value.length - 1)
	var labels = getLabels()
	var output = labels.TH

	switch (lastNumber) {
		case '1':
			if (value != '11') output = labels.ST
			break
		case '2':
			if (value != '12') output = labels.ND
			break
		case '3':
			if (value != '13') output = labels.RD
			break
	}

	return (includeDate ? value : '') + output
}

// YYYY - 2017
// YY - 17
// MMMM - September
// MMM - Sept
// MM - 09, 10, 11
// M - 9, 10, 11
// DDDD - Monday
// DDD = Mon
// DD - 08, 09, 10 (date)
// D - 8, 9, 10
// o - st, nd, rd, th
// Do - 1st, 2nd, 3rd, 4th
// HH - 0...23 > 09, 10, 11
// H - 0...23 > 9, 10, 11
// hh - 1...12 > 09, 10, 11
// h - 1...12 > 9, 10, 11
// mm - 0...59 > 09, 10, 11
// m - 0...59 > 9, 10, 11
// ss - 0...59 > 09, 10, 11
// s - 0...59 > 9, 10, 11
// T - 7:00, 8:15
// TT - 19:00, 20:15
// t - 7, 8:15
// tt - 19, 20:15
// a - am pm
// z - timezone abbreviation
